.dropdown {
  position: relative;
  display: inline-block;
  .mainmenubtn {
    //color: white
    border: none;
    cursor: pointer;
    //padding: 20px
    margin-top: 3px; }
  .dropdown-child {
    //display: none
    //opacity: 0
    overflow: hidden;
    height: 0;
    transition: all 0.5s ease-out;
    background-color: #fff;
    border-radius: 3px;
    min-width: 285px;
    position: absolute;
    right: 1%;
    font-size: 13px;
    a {
      width: 100%;
      color: white;
      text-decoration: none;
      display: block;
      &:after {
        transition: unset;
        border-bottom: unset; }
      &:hover:after, &:focus:after {
        width: 0; } } } }
.dropdown:hover .dropdown-child {
  overflow: visible;
  height: 41px;
  //padding: 5px 5px
  transition: all 0.5s ease-out; }
.dropdown:hover a {}
