@import "../../variables_form7";

.countryListForm7 {
  position: relative;

  input {
    width: 196px;
    border: 1px solid $main-border-color;
    border-radius: 5px;
    padding-bottom: 6px;
    padding-top: 6px;
    appearance: none;
    outline: none;
    box-sizing: border-box;

    &[placeholder] {
      color: rgba(0, 0, 0, 0.6);
      padding-left: 5px; }

    &:active, &:focus {
      border: 1px solid $input-border-color;
      box-shadow: 0 0 5px $input-shadow-color; } } }

.selectListForm7 {
  width: 185px;
  border: 1px solid $input-border-color;
  border-radius: 3px;
  color: $input-country-text-color;
  letter-spacing: 1px;
  background-color: $country-bg-color;
  position: absolute;
  z-index: 1;
  top: 14px;
  left: 7px;
  margin-left: 0;
  padding-left: 0;
  text-align: left;

  li {
    list-style-type: none;
    margin-left: 0;
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 4px;

    &:hover {
      background-color: $input-shadow-color; } } }

.errorsCountryForm7 {
  width: 240px;
  border: 1px solid $error-color; }

.inputFeedbackCountryForm7 {
  position: absolute;
  top: 28px;
  right: 8px;
  color: $error-color;
  font-size: 11px;
  letter-spacing: 0.6px; }

@import "CountryInput_320";
